.services__container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 3rem;
    justify-content: center;


}
.service{
    background:var(--color-bg-variant);
    border-radius: 0 0 2rem 2rem ;
    border: 1px solid var(--color-primary);
    height: fit-content;
    transition: var(--transition);

}
.service:hover{
    background: transparent;
    border-color: 1px solid var(--color-bg-variant);
    cursor: default;
}
.service__head{ 
    background: var(--color-primary);
    padding: 2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1);
}
.service__head h3{
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}
.service__list{
    padding: 2rem;
}
.service__list li{
    display: flex;
   gap: 2rem;
}
.service__list-icon{
    color: var(--color-primary);
    margin-top: 3px;
}
@media screen and (max-width: 1024px){
    .services__container{
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    .service{
        height: auto;
    }
}
@media screen and (max-width: 600px){

    .services__container{
        grid-template-columns: 1fr;
        gap: 1.5rem;
       
    }
}